@import url(https://fonts.googleapis.com/css?family=Sriracha&display=swap);
@import url(https://fonts.googleapis.com/css?family=Poppins&display=swap);
* {
  margin: 0;
  padding: 0;
}

body {
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.Header {
    font-family: 'Sriracha', cursive;
    min-height: 10vh;
    width: 100%;
    background: -webkit-gradient(linear,left top, left bottom,from(#f53d2d),to(#f63));
    background: linear-gradient(-180deg,#f53d2d,#f63);
    display: flex;
    justify-content: center;
    align-items: center;
    -webkit-user-select: none;
       -moz-user-select: none;
        -ms-user-select: none;
            user-select: none;
}

.Header #logo {
    width: 7.5vh;
    margin-right: 1rem;
}

.Header #brand {
    color: white;
    font-size: 1.5rem;
}

@media screen and (max-width: 768px) {
    .Header #brand {
        font-size: 1.2rem;
    }
}
.Main {
    background-color: white;
    width: 100%;
    min-height: 82vh;
    display: flex;
    flex-direction: column;
    align-items: center;
}
.GetUris {
    width: 85%;
    margin-bottom: 3rem;
}

.GetUris form {
    width: 100%;
    margin: 2rem 0px;
    display: flex;
    flex-direction: column;
    align-items: center;
}

.GetUris input {
    width: 80%;
    height: 2.5rem;
    background-color: rgba(0, 0, 0, 0.05);
    border: none;
    padding: 0 1rem;
    font-size: 1.05rem;
    border-radius: 1rem;
    outline: none!important;    
}

.GetUris .get {
    cursor: pointer;
    width: 4rem;
    height: 2.5rem;
    margin: 1rem 0 2rem 0;
    border-radius: 0.25rem;
    border: none;
    background-color: #28a745;
    color: white;
    -webkit-user-select: none;
       -moz-user-select: none;
        -ms-user-select: none;
            user-select: none;
    font-size: 1rem;
    outline: none!important;
}

.GetUris .get:hover {
    background-color: #1d8d37;
}

.GetUris .result {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
}

.GetUris .result div {
    width: 20%;
    margin: 10px 10px;
    box-shadow: 0 2px 12px rgba(0, 0, 0, 0.3);
}

.GetUris .result img {
    width: 100%;
    height: auto;
}

@media screen and (max-width: 1000px) {
    .GetUris .result div {
        width: 30%;
    }
}

@media screen and (max-width: 768px) {
    .GetUris {
        width: 100%;
    }

    .GetUris .result div {
        width: 40%;
    }
}

@media screen and (max-width: 400px) {
    .GetUris .result div {
        width: 90%;
    }
}
.Footer {
    min-height: 8vh;
    width: 100%;
    background: #e7e7e7;
    display: flex;
    justify-content: space-between;
    align-items: center;
    color: rgba(0, 0, 0, 0.65);
    -webkit-user-select: none;
       -moz-user-select: none;
        -ms-user-select: none;
            user-select: none;
}

.Footer div {
    margin: 0 1rem;
}

.App {
  font-family: 'Poppins', sans-serif;
  min-height: 100vh;
  width: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
